@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body{margin:0;padding:0;min-height:100%}

.jumbotron{background:#22c391;background-repeat:none;background-size:cover;background-position:center;display:flex;justify-content:center;align-items:center;text-align:center;color:white;font-size:3em;min-height:100vh}.jumbotron *{margin:0;font-weight:200}

.blink{-webkit-animation:blink 1s step-end infinite;animation:blink 1s step-end infinite}@-webkit-keyframes blink{50%{visibility:hidden}}@keyframes blink{50%{visibility:hidden}}

.root{padding-bottom:2em}.timeline{position:relative;min-height:100%;max-width:80%;margin:0 auto}.timeline::after{content:'';position:absolute;width:6px;background-color:white;top:0;bottom:0;left:50%;margin-left:-3px}.container{padding:10px 40px;position:relative;background-color:inherit;width:50%}.container::after{content:'';position:absolute;width:24px;height:24px;right:-12px;background-color:#5000ca;top:18px;border-radius:50%;z-index:1}.left{left:0}.right{left:50%}.left::before{content:" ";height:0;position:absolute;top:22px;width:0;z-index:1;right:30px;border:medium solid white;border-width:10px 0 10px 10px;border-color:transparent transparent transparent white}.right::before{content:" ";height:0;position:absolute;top:22px;width:0;z-index:1;left:30px;border:medium solid white;border-width:10px 10px 10px 0;border-color:transparent white transparent transparent}.right::after{left:-12px}.content{padding:20px 30px;background-color:white;position:relative;border-radius:6px}@media screen and (max-width: 600px){.timeline::after{left:31px}.container{width:100%;padding-left:70px;padding-right:25px}.container::before{left:60px;border:medium solid white;border-width:10px 10px 10px 0;border-color:transparent white transparent transparent}.left::after,.right::after{left:18px}.right{left:0%}}

